import React, { Component } from "react";
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import "./TodoApp.css";

class TodoApp extends Component {
  state = {
    input: "",
    items: [],
    dates: [],
    editIndex: -1,
    editedItem: "",
    selectedDate: null
  };
  handleChange = (event) => {
    this.setState({
      input: event.target.value,
    });
  };

  storeItems = (event) => {
    event.preventDefault();
    const { input, items, dates, editIndex, selectedDate  } = this.state;

    if (editIndex === -1) {
      this.setState({
        items: [...items, input],
        dates: [...dates, selectedDate],
        input: "",
        selectedDate: null
        
      });
    } else {
      const updatedItems = [...items];
      updatedItems[editIndex] = input;

      const updatedDates = [...dates];
      updatedDates[editIndex] = selectedDate;
  

      this.setState({
        items: updatedItems,
        dates: updatedDates,
        input: "",
        editIndex: -1,
        editedItem: "",
        
      });
    }
  };

  deleteItem = (key) => {
    this.setState({
      items: this.state.items.filter((data, index) => index !== key),
    });
  };
  editItem = (index) => {
    const { items } = this.state;
    this.setState({
      input: items[index],
      editIndex: index,
      editedItem: items[index],
    });
  };

  render() {
    const { input, dates, items, selectedDate } = this.state;
  
    return (
      <div className='todo-container'>
        <form className='input-section' onSubmit={this.storeItems}>
          <h1>TodoApp</h1>
          <input
            type="text"
            placeholder='Enter Items...'
            value={input}
            onChange={this.handleChange}
          />
          <DatePicker
            selected={selectedDate}
            onChange={date => this.setState({ selectedDate: date })}
            placeholderText="Select a date"
          />
          <button type="submit">Add</button>
        </form>
        <ul>
  {items.map((data, index) => (
    <li key={index}>
      <div className="todo-info">
        <span className="todo-date" >{dates[index]?.toDateString()}</span>
        <span className="todo-item">{data}</span>
      </div>
      <div className="todo-actions">
        <i
          className="fa-solid fa-trash-can"
          onClick={() => this.deleteItem(index)}
        ></i>
        <i
          className="fa-solid fa-edit"
          onClick={() => this.editItem(index)}
        ></i>
      </div>
    </li>
  ))}
</ul>

      </div>
    );
  }
}  

export default TodoApp;
